@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Ubuntu:wght@300;400;500&display=swap);
body {
  background-color: rgba(16, 15, 15, 0.95) !important;
  color: white !important;
  margin: 0 !important;
  padding: 0 !important;
  font-family: 'PT Sans', sans-serif;
}

section {
  padding: 55px 100px 15px 0px;
}

a {
  text-decoration: none !important;
}

.section-headers {
  padding-bottom: 25px;
}

.badge {
  border: 1px solid white;
  background-color: transparent;
  margin-right: 10px;
}


.sidebar-padding {
  padding-left: 70px;
}

.item-block {
  padding: 5px 10px 35px;
  border: 1px solid transparent;
  border-radius: 25px;
}

.item-block:hover {
  background-color: rgba(44, 42, 42, 0.95);
  transition: 0.3s;
}

.nav-item a:hover {
  color: white !important;
  transition: 0.3s;
}

.active {
  color: white;
}

.covering {
  margin: 0;
  height: 100%;
  padding-top: 55px;
  font-family: 'PT Sans', sans-serif;
}

/* 
.sticky-sidebar {
  position:fixed;
  max-width: 20%;
  } */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-size {
  font-size: 0.5rem;
}

.gray-text {
  color: gray !important;
}

.gray-section {
  background-color: gray;
}

.white-text {
  color: white;
}

.experience-headers {
  color: white;
  font-size: 1.1rem;
  padding-bottom: 5px;
}

.experience-subheaders {
  font-size: 1.1rem;
  padding-bottom: 5px;
}

.header-icon {
  font-size: 25px;
  padding-right: 25px;
  color: gray;
}

.header-icon:hover {
  color: white;
}

.link-arrow {
  font-size: 11px;
  padding-left: 1%;
}

.footer{
  font-size: 13px;
  text-align: left;
  padding: 20px;
}

.footer span{
  color: rgb(190, 178, 178);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.new-container-fluid {
  padding: 3% 15%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

